export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"mixins/expander-base\";\n\n:host {\n    .app-filtered-view-with-list-list-repeat {\n        margin: @nui-space-sm @nui-space-md;\n        width: 100%;\n    }\n\n    .app-filtered-view-with-list-list-selector {\n        flex-grow: 0;\n        margin-left: @nui-space-md;\n    }\n\n    .app-filtered-view-with-list-list-sorter {\n        flex-grow: 0;\n        margin-left: @nui-space-md;\n    }\n\n    .app-filtered-view-with-list-list-search {\n        flex-grow: 1;\n        margin-right: @nui-space-md;\n    }\n\n    .app-filtered-view-with-list-list-paginator {\n        flex-grow: 1;\n        margin: 0 @nui-space-md;\n    }\n}\n\n.nui-tree {\n    .expander();\n\n    &__body {\n        &--collapsed {\n            overflow: hidden;\n        }\n    }\n\n    &__leaf {\n        display: flex;\n        align-items: center;\n    }\n\n    &__invisible {\n        display: none;\n    }\n}\n";