export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"nui-framework-spaces\";\n@import (reference) \"mixins\";\n\n.filter-group {\n    &__header {\n        .setCssVariable(color, nui-color-text-default);\n        font-weight: bold;\n        line-height: @nui-line-height-default;\n        text-overflow: ellipsis;\n    }\n\n    &__show-more-link {\n        cursor: pointer;\n        padding: @nui-space-xs + @nui-space-xxs;\n    }\n\n    &__checkbox {\n        &-value {\n            flex: 0 1 auto;\n            overflow: hidden;\n            text-overflow: ellipsis;\n            white-space: nowrap;\n        }\n\n        &-count {\n            flex: 0 0 auto;\n            margin-left: @nui-space-xs;\n            text-align: right;\n        }\n    }\n}\n";